<template>
   <v-app>
      <v-main>
         <v-container fill-height>
            <v-layout align-center justify-center>
               <v-col xs10 sm10 md6 lg4>
                  <v-card class="elevation-10">
                     <v-img gradient='90deg, rgba(251,213,61,1) 0%, rgba(250,158,0,1) 20%, rgba(215,0,2,1) 100%'>
                     <v-toolbar dark color="transparent">
                        <v-toolbar-title>
                           {{isRegister ? stateObj.register.title : stateObj.login.title}}
                        </v-toolbar-title>
                     </v-toolbar>
                     </v-img>
                     <v-card-text>
                     <form ref="form" @submit.prevent="isRegister ? register() : submitLogin()">
                        <v-row>
                           <v-col>
                              <v-text-field
                              v-model="email"
                              name="email"
                              label="Email"
                              type="text"
                              placeholder="Email"
                              required
                              ></v-text-field>
                           </v-col>
                        </v-row>
                           
                        <v-row>
                           <v-col>
                              <v-text-field
                              v-model="password"
                              name="password"
                              label="Password"
                              type="password"
                              placeholder="Password"
                              required
                              ></v-text-field>
                           </v-col>
                           <v-col v-if="isRegister">
                              <v-text-field 
                              v-model="confirmPassword"
                              name="confirmPassword"
                              label="Confirm Password"
                              type="password"
                              placeholder="Confirm Password"
                              required
                              ></v-text-field>
                           </v-col>
                        </v-row>

                        <v-row v-if="isRegister">
                           <v-col>
                              <v-text-field
                              v-model="firstName"
                              name="firstName"
                              label="First Name"
                              type="text"
                              placeholder="Your given name"
                              required
                              ></v-text-field>
                           </v-col>
                           <v-col>
                              <v-text-field 
                              v-model="lastName"
                              name="lastName"
                              label="Last Name"
                              type="text"
                              placeholder="Your family name"
                              required
                              ></v-text-field>
                           </v-col>
                           <v-col>
                              <v-text-field 
                              v-model="phone_number"
                              name="phone_number"
                              label="Phone Number"
                              type="text"
                              placeholder="Your phone number"
                              required
                              ></v-text-field>
                           </v-col>
                        </v-row>

                        <div class="red--text"> {{errorMessage}}</div>

                        <v-row 
                        align='center'
                        justify='center'>

                           <v-col 
                           align='left'
                           class='col-md-7 col-sm-12 col-xs-12'>
                              <v-btn rounded class="info" v-on:click="isRegister = !isRegister;">
                                 {{toggleMessage}}  
                              </v-btn>
                           </v-col>

                           <v-col 
                           align='right'
                           class='col-md-3 col-sm-12 col-xs-12'>  
                              <v-btn rounded type="submit" color="primary" value="log in">{{password_reset_button}}</v-btn>
                           </v-col>
                           <v-col 
                           align='right'
                           class='col-md-2 col-sm-12 col-xs-12'>  
                              <v-btn rounded type="submit" color="primary" value="log in">{{isRegister ? stateObj.register.name : stateObj.login.name}}</v-btn>
                           </v-col>
                        </v-row>

                      </form>
                     </v-card-text>
                  </v-card>
                
               </v-col>
            </v-layout>
         </v-container>
      </v-main>
   </v-app>
</template>

<script>
import axios from 'axios';
import { mapActions } from 'vuex';

export default {
   name: 'Login',

   components: {
   },

   data() {
   return {
      email: "",
      password: "",
      confirmPassword: "",
      firstName: "",
      lastName: "",
      phone_number: "",
      isRegister : false,
      errorMessage: "",
      stateObj: {
         register :{
            title: 'Регистрация',
            name: 'Създай потребител',
            message: 'Върни се към вход'
         },
         login : {
            title: 'Потребителски панел',
            name: 'Влез',
            message: 'Регистрирай се'
         }
      },
      password_reset_button: "Забравена Парола"
   };
   },

methods: {
   ...mapActions(['logIn']),
    async submitLogin() {
      const User = new FormData();
      User.append('username', this.email);
      User.append('password', this.password);
      await this.logIn(User);
      this.$router.push('/home');
    },

   async register() {
      if(this.password == this.confirmPassword){

         let payload = {
            'email': this.email,
            'password': this.password,
            'first_name': this.firstName,
            'last_name': this.lastName,
            'phone_number': this.phone_number
         }
         
         let request = await axios.post('/register', payload)
         console.log(request)
         if(request.data[0].status == 'success'){
            this.isRegister = false;
            this.errorMessage = "";
         }  else if(request.data[0].status == 'emailExists') {
            this.errorMessage = "Email already exists!";
         }
         
      }
      else {
         this.errorMessage = "Passwords do not match!"
      }
   }
},

   computed: {
      toggleMessage : function() { 
         return this.isRegister ? this.stateObj.register.message : this.stateObj.login.message }
   }
}
</script>
